import React from 'react';
import {
  Box,
  Container,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Typography,
  CardActions,
  Button,
} from '@mui/material';
import mentoring from '../images/mentoring.jpg';
import roompot from '../images/roompot-groups.jpg';
import europarks from '../images/europarks.jpg';
import castle from '../images/castle.jpg';
import marlin from '../images/marlin.jpg'
import coditas from '../images/coditas.jpg'
import tgi from '../images/TGI.jpg'
import codinu from '../images/codinu.jpg'
import hdfc from '../images/hdfc.jpg'

export default function Projects() {
  const projects = [
    {
      title: 'MISB Mentoring Program',
      description:
        'Developed a responsive mentoring website using HTML, CSS/SASS, JavaScript, applying strong UI/UX principles. Responsible for ongoing maintenance, functionality enhancements, and accessibility by following WCAG guidelines.',
      image: mentoring,
      link: 'https://ilitchbusiness.xinspire.com/',
    },
    {
      title: 'Codinu',
      description:
        'Developed the Codinu website using Strapi CMS, ReactJS, Tailwind CSS, and HTML. Ensured responsive design, improved performance, and closely collaborated with UI/UX team to convert Figma-based designs into user-friendly layouts.',
      image: codinu,
      link: 'https://www.codinu.com/',
    },
    {
      title: 'HDFC',
      description:
        'Contributed in development of HDFC\'s Life Insurance module using ReactJS, Redux, Material-UI, and PostgreSQL. Integrated APIs, collaborated with backend developers, product owners, and business analysts to deliver key features.',
      image: hdfc,
      link: 'https://www.hdfclife.com/',
    },
        {
      title: 'Coditas',
      description:
        'Improved Coditas\' internal website by implementing a responsive UI with ReactJS, TypeScript, SASS, and Bootstrap. Collaborated with designers to translate Figma prototypes into functional and polished web interfaces.',
      image: coditas,
      link: 'https://coditas.com/',
    },
    {
      title: 'TGI Sports',
      description:
        'Developed a full-featured website from scratch leveraging ReactJS, SASS, Material-UI, Strapi CMS, ExpressJS, and PostgreSQL, incorporating SEO best practices.',
      image: tgi,
      link: 'https://tgisport.com/',
    },

    {
      title: 'Marlin',
      description:
        'Engaged in the development and maintenance of products, UI improvements, requirement collection, debugging, and code optimization using ReactJS, SASS, and Bootstrap, all while working under the SCRUM framework.',
      image: marlin,
      // link: 'https://your-castle-link.com',
    },
    {
      title: 'Castle Rental Platform',
      description:
        'Executed tasks such as product development, maintenance, UI optimization, requirement analysis, bug fixing, and code refinement using ReactJS, SASS, and Bootstrap, within an Agile SCRUM environment.',
      image: castle,
      link: 'https://www.castleresorts.com/',
    },
    {
      title: 'Europarcs UI Redesign',
      description:
        'Worked on frontend development tasks like collecting requirements, building new products, maintaining features, improving UI, fixing bugs, and refining code. Used ReactJS, SASS, and Bootstrap adhering to SCRUM practices.',
      image: europarks,
      link: 'https://www.europarcs.com/',
    },
    {
      title: 'Roompot',
      description:
        'Contributed to new product development, ongoing maintenance, frontend UI enhancements, requirement gathering, bug resolution, and code cleanup using ReactJS, SASS, and Bootstrap, following SCRUM methodology.',
      image: roompot,
      link: 'https://www.roompot.com/',
    },
  ];

  return (
    <Box sx={{ py: 10, bgcolor: '#ffffff' }}>
      <Container maxWidth="lg"> {/* medium container to constrain width */}
        <Typography variant="h4" gutterBottom>
          Projects
        </Typography>

        <Grid container spacing={4} justifyContent="center">
          {projects.map((project, index) => (
            <Grid
              item
              xs={12}
              sm={6} // 2 columns from small breakpoint and above
              key={index}
              sx={{
                display: 'flex', // stretch cards to equal height
                justifyContent: 'center',
              }}
            >
              <Card
                sx={{
                  width: 360, // fixed width for cards
                  height: 480, // fixed height for cards
                  display: 'flex',
                  flexDirection: 'column',
                  borderRadius: 2,
                  overflow: 'hidden',
                  boxShadow: 3,
                  transition: 'transform 0.3s ease',
                  '&:hover': {
                    transform: 'scale(1.03)',
                  },
                }}
              >
                <CardMedia
                  component="img"
                  height="250"
                  image={project.image}
                  alt={project.title}
                  sx={{ objectFit: 'cover' }}
                />

                <CardContent sx={{ flexGrow: 1 }}>
                  <Typography variant="h6" gutterBottom noWrap>
                    {project.title}
                  </Typography>
                  <Typography
                    variant="body2"
                    color="text.secondary"
                    sx={{
                      display: '-webkit-box',
                      WebkitLineClamp: 5,
                      WebkitBoxOrient: 'vertical',
                      overflow: 'hidden',
                    }}
                  >
                    {project.description}
                  </Typography>
                </CardContent>

                <CardActions sx={{ justifyContent: 'flex-end', px: 2, pb: 2 }}>
                  <Button
                    size="small"
                    href={project.link}
                    target="_blank"
                    rel="noopener noreferrer"
                    variant="contained"
                    sx={{backgroundColor: '#863A6F',          // your custom fill color (green here)
                    '&:hover': {
                      backgroundColor: '#9A5E88',        // darker shade on hover
                    },
                  }}
                  >
                    View
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
}