import React, { useState } from 'react';
import {
  AppBar, Toolbar, Box, Button, IconButton,
  Drawer, List, ListItem, ListItemButton, ListItemText, Typography
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link } from 'react-scroll';
import '../styles/styles.scss';

const sections = ['home', 'about', 'skills', 'projects', 'contact'];

export default function Navbar() {
  const [drawerOpen, setDrawerOpen] = useState(false);

  const drawerItems = (
    <Box sx={{ width: 250 }}>
      <List>
        {sections.map((section) => (
          <ListItem key={section} disablePadding>
            <ListItemButton
              component={Link}
              to={section}
              activeClass="active"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              onClick={() => setDrawerOpen(false)}
            >
              <ListItemText primary={section.charAt(0).toUpperCase() + section.slice(1)} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <>
      <AppBar className='navigation' position="fixed" color="primary" sx={{ zIndex: 1201 }}>
        <Toolbar sx={{ display: 'flex', justifyContent: 'space-between' }}>
          <Typography variant="h4" sx={{ fontWeight: 'bold' }}>PORTFOLIO</Typography>

          {/* Desktop Links */}
          <Box sx={{ display: { xs: 'none', md: 'flex' }, gap: 2 }}>
            {sections.map((section) => (
              <Button
                key={section}
                color="inherit"
                component={Link}
                to={section}
                activeClass="active"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
                sx={{
                  '&.active': {
                    borderBottom: '2px solid white',
                    fontWeight: 'bold',
                  }
                }}
              >
                {section.charAt(0).toUpperCase() + section.slice(1)}
              </Button>
            ))}
          </Box>

          {/* Mobile Menu Icon */}
          <IconButton
            edge="end"
            color="inherit"
            aria-label="menu"
            sx={{ display: { xs: 'flex', md: 'none' } }}
            onClick={() => setDrawerOpen(true)}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      {/* Drawer for Mobile */}
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        {drawerItems}
      </Drawer>
    </>
  );
}
