import React from 'react';
import { Box, Typography, Container } from '@mui/material';
import myPhoto from '../images/professional-photo.png';
import '../styles/styles.scss';

export default function Home() {
    return (
        <Box sx={{pb:10, pt: 20, bgcolor: '#FFE6E6' }}>
            <Container className="profile-section">
                <div className="profile-flex">
                    <div className="myPhotoContainer">
                        <img src={myPhoto} alt="Sayali More" />
                    </div>
                    <div className="profile-text">
                        <Typography variant="h3" gutterBottom>Hi, I'm Sayali More</Typography>
                        {/* <Typography variant="h5">Frontend-UI/UX Developer</Typography> */}
                        <p className='introText'>I'm a Front-End UI/UX Developer with over 7 years of experience crafting responsive, accessible, and user-centric web applications. I specialize in building scalable front-end architectures using React, TypeScript, and modern design systems.
                            I work closely with design tools like Figma and Adobe XD to translate ideas into clean, reusable components. With hands-on experience in Material UI and Tailwind CSS, I create consistent, modern interfaces that are both visually engaging and developer-friendly.
                            Collaboration and version control are part of my workflow. I'm proficient with Git and GitHub, and thrive in Agile environments where code quality, performance, and design precision matter.
                            I’m passionate about UI/UX, performance optimization, and writing maintainable code that brings designs to life across devices and platforms.</p>
                            <h4>MS Computer Science, Wayne State University <br /> Email: sayali.more@wayne.edu | sayalimore12@gmail.com <br/>Phone: +1(201)681-2945</h4>
                            
                    </div>
                </div>
            </Container>
        </Box>
    );
}
