import React from 'react';
import { Box, Container, Typography, Grid, Paper } from '@mui/material';

const skills = [
  'C','HTML5', 'CSS/CSS3', 'SASS', 'Tailwind', 'JavaScript', 'TypeScript','React.js', 'Redux', 'Express.js','NodeJS', 'Material UI', 'Bootstrap', 'RESTful APIs', 'GraphQL','Axios','Ajax','JSON','.Net Web APIs','PostgreSQL','MySQL','MongoDB','Strapi CMS','Wordpress','TDD','Unit Testing','SEO', 'Figma', 'Invision', 'Git', 'Responsive Design', 'Agile','Scrum', 'CI/CD', 'DevOps','Jira','Leadership','Problem-solving','Adaptability'
];

export default function Skills() {
  return (
    <Box sx={{ py: 10, bgcolor: '#f9f9f9' }}>
      <Container>
        <Typography variant="h4" gutterBottom>Skills</Typography>
        <Grid container spacing={2}>
          {skills.map((skill, index) => (
            <Grid item xs={6} sm={4} md={3} key={index}>
              <Paper elevation={3} sx={{ p: 2, textAlign: 'center' }}>
                {skill}
              </Paper>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
}
